import React from 'react'
import reactstrap, { Button } from 'reactstrap'

// import {useNavigate} from "react-router-dom"

export default function Card() {
  // const nav= useNavigate();
  // const click=(e)=>{
  //   nav('/achiev');}
  const img1=require('./img1.jpeg');
  const img2=require('./img2.jpeg');
  const img3=require('./img3.jpeg');
  return (
    <>
  <div className="card-group">
  <div className="card">
    <img className="card-img-top" src={img1}/>
    <div className="card-body">
      <h5 className="card-title">Web Development</h5>
      <p className="card-text">
        Click ! the below button to see web based projects created by me ......
      </p>
      <p className="card-text">
        <small className="text-muted">Last updated 30 mins ago</small>
      </p>
      <button>Web Development</button>
    </div>
  </div>
  <div className="card">
    <img className="card-img-top" src={img2}  />
    <div className="card-body">
      <h5 className="card-title">App Development</h5>
      <p className="card-text">
        Click! the button bellow to see mobile based applications (both android and ios) created by me
      </p>
      <p className="card-text">
        <small className="text-muted">Last updated 2hrs mins ago</small>
      </p>
      <button>App Development</button>
    </div>
  </div>
  <div className="card">
    <img className="card-img-top" src={img3}  />
    <div className="card-body">
      <h5 className="card-title">Software Development</h5>
      <p className="card-text">
       click! the below button to see the softwares and programs created be me....
      </p>
      <p className="card-text">
        <small className="text-muted">Last updated 1hr  ago</small>
      </p>
      <button>Software Development</button>
    </div>
  </div>
</div>
    </>
  )
}
